    /**
     * 
     */
    INQUIRY_CANCEL: boolean;
    /**
     * QR
     */
    INQUIRY_QRCODE: boolean;
    /**
     * 
     */
    INQUIRY_PRINT: boolean;
    /**
     * 
     */
    INQUIRY_PRINT_EXPIRED_VALUE: string;
    /**
     * 
     */
