    /**
     * 
     */
    ORDER_CANCEL_MAIL_CUSTOM: boolean;
    /**
     * 
     */
    ORDER_PRINT: boolean;
    /**
     * 
     */
    ORDER_LINK: { name: { ja: string; en: string; }; url: string; params: { key: string; value?: string; }[] }[];
    /**
     * QR
     */
    PRINT_QRCODE_TYPE: 'None' | 'token' | 'Custom';
    /**
     * QR
     */
    PRINT_QRCODE_CUSTOM: string;
    /**
     * 
     */
    PRINT_LOADING: boolean;
